  value_discount_point: number = 0;
  point_validity: number = 0;
  /**
  * tnh li gi tr thanh ton bng tin vi s im s dng tng ng trong chng trnh tch im
  * @param value im s dng
  * @example this.calculatePoint2Money(10)
  */
  calculatePoint2Money(value) {
    if (value <= this.point_validity) {
      this.value_discount_point = value;

      this.invoice.payment_points = ((value / this.customer_class.payment.points) * this.customer_class.payment.money)
      this.invoice.use_points = (value)
    }
    else {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vt qu gii hn im cho php"))
    }
  }
  /**
    * bt sk thay i gim gi bill theo %
    * @param value % gim
    * @example this.editPercentDiscount(10) 
    */
  editPercentDiscount(value) {
    if (parseFloat(value) > 100)
      document.getElementById("percent-discount-price")['value'] = 0
    else this.percent_discount_bill = value;
    this.invoice.discount = this.subTotal * (this.percent_discount_bill / 100)
    this.setTotal()
    if (!this.gotten_gift_type7) this.handlePromotionType7(this.bill_details_pmtype_7)
  }
  product_codes: any = [];
  /**
 * kim tra m coupon hp l hay ko , tnh s tin thanh ton theo coupon tng ng
 * @param code 
 * @example getDiscountOfCoupon('KDJWOIHA')
 */
  getDiscountOfCoupon(code) {
    if (code)
      this.vhQuerySales.getCoupon_Code_Name_Release_byCode(code)
        .then((product_codes: any) => {
          if (product_codes && !this.product_codes.find(item => item.code == code)) {
            switch (product_codes.status) {
              case 1: {
                this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Phiu ny cha c quy i"))
                break;
              }
              case 3: {
                if (new Date() < new Date(product_codes.date_expire)) {
                  this.product_codes.push(product_codes)
                  this.invoice.payment_coupons += (product_codes.value)
                  this.vhComponent.alertMessageDesktop("success", this.languageService.translate("S dng coupon thnh cng"))
                }
                else {
                  this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Phiu ny  ht hn s dng"), 8000)
                }
                break;
              }
              case 4: {
                this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Phiu ny  c s dng"), 8000)
                break;
              }
            }
          }
          if (!product_codes) this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Khng tm thy phiu ny"), 8000)
          setTimeout(() => {
            document.getElementById('coupon').getElementsByTagName("input")[0].value = '';
          }, 200);
        })

  }
  /**
   * xa coupon  thm, tnh li tin thanh ton bng coupon
   * @param item 
   * @example this.onCloseProductCode(coupon_selected)
   */
  onCloseProductCode(item) {
    this.product_codes.splice(this.product_codes.findIndex(i => i._id == item._id), 1);
    this.invoice.payment_coupons -= item.value
  }
  invoice_fee: any;
