  cqt_invoice_auto_sign: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_invoice_auto_sign
  cqt_submit_invoice_immediately: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_submit_invoice_immediately

  handleMinvoice(bill) {
    console.log(bill);
    bill = { ...this.getCreateUpdateInvoice(1) }
    return new Promise((resolve, reject) => {
      // ly ra chi nhnh hin ti
      let currentBranch = this.vhQuerySales.getlocalBranch(this.vhQuerySales.getDefaultBranch()._id);

      if (!currentBranch.einvoices) {
        resolve(true)
        return
      }
      // trng hp cha c to kt ni ha n in t
      if (!currentBranch.einvoices?.length) {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
        resolve(true)
        return
      }
      // trng hp  gi ln cqt ri th khi
      if(bill.cqt_status == 2 || bill.cqt_status == 3){
        resolve(true)
        return
      }
      // trng hp ch c 1 kt ni ha n in t th to thng lun
      if (currentBranch.einvoices.length && currentBranch.einvoices.length == 1) {
        if (!currentBranch.einvoices[0].invoiceSeries.length) {
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
          resolve(true)
          return
        }
        this.billService.changeBill_byLocal_toInvoice_byMinvoice(bill._id, currentBranch.einvoices[0].invoiceSeries[0])
          .then((rsp) => {
            console.log('changeBill_toInvoice_byMinvoice', rsp)
            if (rsp.vcode == 0) {
              if (this.cqt_invoice_auto_sign) {
                this.vhEinvoice.saveInvoice_Sign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log('saveInvoice_Sign_byMinvoice', res);
                    if (res.code == '00') {
                      bill.cqt_status = 3
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 3 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {
                      resolve(true)
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {
                      resolve(true)
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
              else {
                this.vhEinvoice.saveInvoice_noSign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log(res);
                    if (res.code == '00') {
                      bill.cqt_status = 2
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 2 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {
                      resolve(true)
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
            }
          }, (error: any) => {
            resolve(true)
            console.log('error', error)
          })
      }
    })
  }
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects(false)

    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesK57Col3Component
      case 'print_size_k80_3c': return SalesK80Col3Component
      case 'print_size_k80_4c': return SalesK80Col4Component
      case 'print_size_a5': return SalesA5Component
      case 'print_size_a4': return SalesA4Component
      default: return SalesA4Component
    }
  }

  /**
   * tr v trang danh sch ha n sau khi  thanh ton xong
   * @example this.handle(true)
   */
  handle() {
    let url = this.url;

    if(this.dataRestore) {
      // X l router t Thng mi in t
      const fromType = this.dataRestore?.shop_info?.from_type;
      if (fromType?.includes('invoice_issued')) {
        const platform = fromType.replace('_invoice_issued', '');
        url = `/sales/dashboard/manage/ecommerce/${platform}/issue-invoice/invoice-issued`;
      } 
      else if (fromType?.includes('invoice_returned')) {
        const platform = fromType.replace('_invoice_returned', '');
        url = `/sales/dashboard/manage/ecommerce/${platform}/issue-invoice/invoice-returned`;
      }

      // replace trong invoice state  trnh li khi quay li
      this.dataRestore.invoice = this.dataRestore?.invoice?.map((invoice) => {
        if (invoice._id === this.invoice._id) {
          invoice['sum_price'] = this.invoice.total * (1 + this.invoice.tax / 100);
          invoice['total'] = this.invoice.total;
        }
        return invoice;
      });
    }

    this.isExit = true;
    this.router.navigate([url], { state: { isSync: true, ...this.dataRestore } })
  }

  /**
   * kim tra d liu thng tin my in v m component in
   * @example this.handlePrint()
   */
  handlePrint() {
    this.invoice_detail.forEach(e => {
      if (e.products_gift) e.products_gift = e.products_gift.filter(item => item.choose)
    })

    this.checkPrint().then((printer: any) => {
      if (printer) {
        this.updateServiceTime();
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: { ...printer, name: "Sales" }, invoice_detail: this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle())
        })
      }
    }, (err) => {
      this.nzModalService.confirm({
        nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
        nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
        nzIconType: "info-circle",
        nzCancelText: null,
        nzOnOk: () => { this.handle() }
      })
    })
  }
