  /**
   * bt sk tt drawer thm khch hng
   * @param result output kt qu tr v
   * @example this.closeAddCustomer('NDWOIUHDIUWQHSIOQUH')
   */
  closeAddCustomer(result) {
    if (this.radioValue == 1) {
      if (result) {
        this.invoice.retail_name = (result.retail_name)
        this.invoice.retail_phone = (result.retail_phone)
        this.invoice.retail_address = (result.retail_address)
      }
      this.invoice.tax_buyerDisplayName = 'Ngi mua khng ly ho n';
      this.invoice.tax_buyerLegalName = '';
      this.invoice.tax_buyerTaxCode = '';
      this.invoice.tax_buyerAddressLine = '';
      this.invoice.tax_buyerEmail = '';
      this.showDrawerAddRetail = false
    }
    else {
      this.showDrawerAddCusomer = false
      if (result) {
        this.invoice.id_customer = result;
        const localCustomer = this.vhQuerySales.getlocalCustomer(result);
        this.invoice.tax_buyerDisplayName = localCustomer?.name || '';
        this.invoice.tax_buyerLegalName = localCustomer?.name || '';
        this.invoice.tax_buyerTaxCode = localCustomer?.taxcode || '';
        this.invoice.tax_buyerAddressLine = localCustomer?.address || '';
        this.invoice.tax_buyerEmail = localCustomer?.email || '';
      }
    }
  }

  barcode: string = ''
  barcode_array = [];
  /**
   * S kin qut barcode bng my
   */
  @HostListener("window: keypress", ["$event"])
  /**
   * S kin nhn barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === this.url + "/edit-sales" && !this.showModalOtherDiscount && !this.isVisibleEditUnitPrice) {
      // if (!this.showSearchBar && !this.showPayInfo) {
      if (_event.key == "Enter") {
        this.barcode_array.forEach((i, index) => {
          if (i == "Shift") {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });
        // this.scanBarcode(this.barcode_array.join(""));
        this.barcode = this.barcode_array.join("");
        setTimeout(() => {
          let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(this.barcode);
          if (detailproduct) {
            this.closeSearchProductList({ ...detailproduct, ...this.vhQuerySales.getUnit_byRatio(detailproduct.units, detailproduct.ratio) });
          }
          else this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Not found"))
          this.barcode_array = [];
        }, 100);

      } else {
        this.barcode_array.push(_event.key);
      }
      // }
    }
  }
