import { NzModalService } from 'ng-zorro-antd/modal';
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales, VhQuery, VhEinvoice } from 'ionic-vhframeworks';
import { SalesA4Component } from 'src/app/sales/components/print/sales/sales-a4/sales-a4.component';
import { SalesA5Component } from 'src/app/sales/components/print/sales/sales-a5/sales-a5.component';
import { SalesK80Col4Component } from 'src/app/sales/components/print/sales/sales-k80-col4/sales-k80-col4.component';
import { SalesK80Col3Component } from 'src/app/sales/components/print/sales/sales-k80-col3/sales-k80-col3.component';
import { SalesK57Col3Component } from 'src/app/sales/components/print/sales/sales-k57-col3/sales-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoice, VhSaleInvoiceDetail, VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';
import { FunctionService } from 'src/app/sales/services/function.service';
import { VhSaleInvoiceDetailEdit, VhSaleInvoiceEdit } from '../sales-desktop-interface';
import { ProductService } from 'src/app/sales/services/product.service';
import { VhType } from 'src/app/sales/interface/vh-type';

@Component({
  selector: 'sales-desktop-bill-type1-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent {
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  @Input() url = '';
  subTotal = 0;
  taxValue = 0
  tableHeight: string;
  public invoiceInfo: any;
  invoice_detail: VhSaleInvoiceDetailEdit[]
  public invoice: VhSaleInvoiceEdit
  display_promotion_selling_price: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  show_discount_product = this.vhAuth.localStorageGET("show_discount_product");
  /** C phi c router n t Invoice hay khng */
  get isFromInvoice(): boolean {
    return !this.dataRestore || !this.dataRestore.shop_info;
  }
  public VhType = VhType;



  constructor(
    private router: Router,
    public vhQuerySales: VhQuerySales,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private modal: NzModalService,
    private fncService: FunctionService,
    private vhEinvoice:VhEinvoice,
    public productService: ProductService,
  ) {
    this.getDataInvoice(this.router.getCurrentNavigation().extras.state.id)


  }

  /**
   * gn bill v bill-detail get c vo bin v get cc thng tin ca nv, khch hng, phng thc thanh ton
   * @example this.init(bill, bill-deails)
   */
  init(invoice: VhSaleInvoiceEdit = null, invoiceDetail: VhSaleInvoiceDetailEdit[] = null) {
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if(invoice.bill_type == 5) {
      this.updateServiceTime();
    }
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQuerySales.getlocalCustomer(this.invoice.id_customer),
        paymentName: this.setPaymentName(this.invoice.payment_type)
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.setSubTotal()
  }
  /**
   * tnh cng tin ha n
   * @example this.setSubTotal()
   */
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      let total_product_gift = 0;
      let total_products = 0;
      if (i.products_gift) {
        total_product_gift = i.products_gift.reduce((prev: number, next) => prev + next.quantity * next.price, 0);
      }
      else if (i.products) {
        total_products = i.products.reduce((prev: number, next) => prev + next.quantity * next.price, 0);
      }
      subTotal += total_products + total_product_gift + (i.price ? i.price * i.quantity : 0);
    }
    this.subTotal = subTotal;
  }

  /**
   * get d liu bill t DB
   * @param id : nhn t state truyn qua
   * @example this.getDataInvoice('DHIWHNXDIWODUHIWDHOWID')
   */
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(id),
        this.vhQuerySales.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)
      })
    })
  }

  /**
   * xa ha n ln DB
   * @example this.deleteInvoice()
   */
  deleteInvoice() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        if (this.vhAuth.checkMyPermission("sell_enable_cancel_bill")) {
          this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the sales invoice")}?`, "OK", this.languageService.translate("Cancel"))
            .then(() => {
              this.vhComponent.showLoading("", "transparent-loading").then(() => {
                this.vhQuerySales.deleteBill_Billdetail(this.invoice._id).then(() => {
                  if(this.invoice.cqt_status != 1){
                    // this.vhEinvoice.deleteInvoice_byMinvoice
                  }
                  if (this.invoice.earning_points_bill) this.vhQuerySales.updateCustomer_byEarnedPoints(this.invoice.id_customer,  this.invoice.earning_points_bill*(-1))
                  this.vhComponent.hideLoading(0).then(() => {
                    this.dataRestore.invoice = this.dataRestore.invoice.filter(e => e._id != this.invoice._id)
                    this.vhComponent.showToast(2000,
                      `${this.languageService.translate("Sales invoice")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                    this.router.navigate([this.url], { state: { isSync: true , ...this.dataRestore} })
                  })
                })
              })
            }, () => { })
        } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })

  }
  /**
   * tr v tn phng thc thanh ton
   * @param type 
   * @returns string
   * @example this.setPaymentName(1)
   */
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQuerySales.getlocalWallet(this.invoice.id_wallet).name
    }
  }

  /**
   * tr v tn unit ca sn phm theo ratio truyn vo
   * @param units 
   * @param ratio 
   * @returns string
   * @example let unit_name = this.getUnit([object], 1)
   */
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }

  /**
   * tr v chui thng tin l theo id l truyn vo
   * @param lots 
   * @param _id 
   * @returns string
   * @example let lot = this.getLotNumber([], 'DNIWUHNDKLJWHDIWUOH')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' + this.fncService.formatDate(lot.date_exp)
  }
  /**
   * tr v chui thng tin a ch ca customer
   * @param partner 
   * @returns string
   * @example let addr = this.renderAddress(customer_info)
   */
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  /**
   * router trang cp nht ha n
   * @example this.goToCart()
   */
  public goToCart() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        if ((this.vhAuth.checkMyPermission("sell_enable_edit_bill") && this.invoice.bill_type == 1) || (this.vhAuth.checkMyPermission("sell_enable_edit_bill_temporary") && this.invoice.bill_type == 5))
          this.router.navigate([this.url + '/edit-sales'], { state: { invoiceDetail: this.invoice_detail, invoice: this.invoice, dataRestore: this.dataRestore } })
        else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })

  }
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer) resolve(printer)
      else rejects(false)
    })
  }

  /**
   * kim tra thng tin d liu trang in v m component in
   * @example this.printInvoice()
   */
  printInvoice() {
    this.checkPrint().then((printer: any) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer.print_size_default), { printer: { ...printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)], name: "Sales" }, invoice_detail: this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${printer.print_size_default}`)
    }, (err) => {
      this.modal.confirm({
        nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
        nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
        nzIconType: "info-circle",
        nzCancelText: null,
        nzOnOk: () => {}
      });
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesK57Col3Component
      case 'print_size_k80_3c': return SalesK80Col3Component
      case 'print_size_k80_4c': return SalesK80Col4Component
      case 'print_size_a5': return SalesA5Component
      case 'print_size_a4': return SalesA4Component
      default: return SalesA4Component
    }
  }
  /**
   * quay v trang danh sch ha n
   * @example this.goBack()
   */
  goBack() {
    let url = this.url;
    if(!this.isFromInvoice) {
      // X l router t Thng mi in t
      const fromType = this.dataRestore?.shop_info?.from_type;
      if (fromType?.includes('invoice_issued')) {
        const platform = fromType.replace('_invoice_issued', '');
        url = `/sales/dashboard/manage/ecommerce/${platform}/issue-invoice/invoice-issued`;
      } 
      else if (fromType?.includes('invoice_returned')) {
        const platform = fromType.replace('_invoice_returned', '');
        url = `/sales/dashboard/manage/ecommerce/${platform}/issue-invoice/invoice-returned`;
      }
    }
    this.router.navigate([url], { state: { isSync: true, ...this.dataRestore } });
  }
  ngAfterViewChecked() {
    // tnh li chiu cao ca table  scroll
    if (document.getElementById('sales-invoice-detail-today') && document.querySelector(".sales-cart-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.getElementById('sales-invoice-detail-today').clientHeight - document.querySelector(".sales-cart-header").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
    }
    this.cdRef.detectChanges();
  }



  /**
   * Tnh li bill detail cho Dch v thi gian v setSubTotal() cho invoices
   */
  updateServiceTime() {
    if(!this.productService.isContainServiceTime(this.invoice_detail)) return;
    this.productService.updateServiceTimePriceInvoiceDetail(this.invoice_detail, this.invoice.price_type);
    this.updateBillCalculations();
  }



  /**
   * Tnh li tt c thng s  Bill.
   *
   * Tnh li subTotal, chit khu, discount, discount_bill, payment_points, payment_coupons, fee, tax, im tch ly cng thm
   */
  updateBillCalculations() {
    // subTotal
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      let total_product_gift = 0;
      let total_products = 0;
      if (i.products_gift) {
        total_product_gift = i.products_gift.filter(item => item.choose).reduce((prev: number, next) => prev + next.quantity * next.price, 0);
      }
      else if (i.products) {
        total_products = i.products.reduce((prev: number, next) => prev + next.quantity * next.price, 0);
      }
      subTotal += total_products + total_product_gift + (i.price ? i.price * i.quantity : 0);
    }
    this.subTotal = subTotal;

    // total
    this.invoice.total = Math.max(0, this.subTotal - this.invoice.discount - this.invoice.discount_bill - (this.invoice.payment_coupons + this.invoice.payment_points) + this.invoice.fee);

    // tax
    this.taxValue = this.invoice.total * (this.invoice.tax / 100);
    
    // payment
    if (this.invoice.payment_type == 2) {
      this.invoice.payment = 0;
    } 
    else {
      this.invoice.payment = this.invoice.total + this.taxValue;
    }

    // cash
    if (this.invoice.payment_type == 2) this.invoice.cash = 0;
    else this.invoice.cash = 0;
  }

}
